import {FC} from 'react';
import notFound from '../assets/notfound.svg'
import {Link} from "react-router-dom";
const NotFound: FC = () => {
    return (
        <div className='not__found'>
            <img src={notFound} alt=""/>
               !!!
            <span>404</span>
            <Link to={'/react-pizza/'} className='back'>
                
            </Link>
        </div>
    );
};

export default NotFound;